        justify-content: flex-end;
        &:first-of-type, &:nth-of-type(2) {
            margin-top: 14.7%;
        } 
        &:nth-of-type(2n) {
            justify-content: flex-start;
        }
    }
    @media(max-width: 768px) {
        width: 173px;
        &_wrapper {
            width: 100%;
